.section__subtitle {
    margin: 0;
    padding: 0;
    font-family: 'YS Text', 'Arial', sans-serif;
    font-style: normal;
    font-weight: 500;
    text-align: center;
    max-width: 1110px;
    font-size: 24px;
    line-height: 32px;
    color: #000000;
}

@media (max-width: 767px) {
    .section__subtitle {
        font-size: 18px;
        line-height: 24px;
    }
}