.section__title {
  margin: 0 auto;
  padding: 0;
  font-family: 'YS Display', 'Arial', sans-serif;
  font-style: normal;
  font-weight: 500;
  text-align: center;
  max-width: 1110px;
  font-size: 40px;
  line-height: 48px;
  color: #000000;
}

@media (max-width: 767px) {
  .section__title {
    font-size: 32px;
    line-height: 40px;
  }
}
